/* App.css */
.App {
  text-align: center;
  margin-top: 50px;
  font-family: Arial, sans-serif;
}

.calculator {
  width: 250px;
  margin: 0 auto;
  border: 2px solid #888;
  border-radius: 10px;
  padding: 15px;
  background: #f9f9f9;
}

.display {
  width: 100%;
  height: 40px;
  font-size: 24px;
  text-align: right;
  margin-bottom: 10px;
  padding-right: 10px;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
}

.btn {
  font-size: 18px;
  padding: 15px;
  border: none;
  background-color: #ddd;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.2s;
}

.btn:hover {
  background-color: #ccc;
}